@import url("https://fonts.googleapis.com/css2?family=Gemunu+Libre:wght@200;300;400;500;600;700;800&display=swap");
.header {
  padding: 1em;
  position: relative;
  display: flex;
  justify-content: space-between;
  align-items: center;
  background: #6A848C;
}
.header__logo {
  height: 4em;
  display: flex;
  align-items: center;
  justify-content: flex-start;
  text-decoration: none;
}
.header__logo img {
  max-height: 6.2em;
  transform: translateY(0px);
}
.header--background {
  border-bottom: 3px solid #6A848C;
}
.header__hamburger {
  height: 5em;
  width: 3em;
  position: absolute;
  right: 3%;
  align-self: center;
  transition: 0.5s;
}
@media screen and (min-width: 768px) {
  .header__hamburger {
    display: none;
  }
}
.header__hamburger .header__line:nth-child(1) {
  position: absolute;
  height: 3px;
  width: 40px;
  border-radius: 1.5px;
  background-color: #FFFFFF;
  top: 35%;
  transition: 0.4s;
}
.header__hamburger .header__line:nth-child(2) {
  position: absolute;
  height: 3px;
  width: 40px;
  border-radius: 1.5px;
  background-color: #FFFFFF;
  top: 50%;
  transition: 0.4s;
}
.header__hamburger .header__line:nth-child(3) {
  position: absolute;
  height: 3px;
  width: 40px;
  border-radius: 1.5px;
  background-color: #FFFFFF;
  top: 65%;
  transition: 0.4s;
}
.header__exit .header__line:nth-child(1) {
  top: 50%;
  transform: rotate(-405deg);
}
.header__exit .header__line:nth-child(2) {
  transform: scale(0);
}
.header__exit .header__line:nth-child(3) {
  top: 50%;
  transform: rotate(405deg);
}
.header__mobile {
  position: absolute;
  width: 100%;
  padding: 1em 0;
  z-index: 2;
  transform-origin: top;
  transform: translateY(240px) translateX(-16px);
  display: flex;
  flex-direction: column;
  align-items: center;
  background-color: #6A848C;
  border-top: 3px solid white;
}
.header__mobile a {
  margin: 21px 0;
  padding-bottom: 0.3em;
  font-size: 2em;
  color: white;
  text-decoration: none;
}
.header__mobile a:hover {
  color: #FFFFFF;
  border-bottom: 2px solid #FFFFFF;
}
@media screen and (max-width: 768px) {
  .header__desktop {
    display: none;
  }
}
.header__desktop a {
  margin: 0 1em;
  padding-bottom: 0.3em;
  font-size: 1.1em;
  color: #FFFFFF;
  text-decoration: none;
  border-bottom: 2px solid #FFFFFF;
}
.header__desktop a:hover {
  color: #FFFFFF;
  border-bottom: 2px solid #FFFFFF;
}

.d-none {
  display: none;
}

.shop-image img {
  width: 100vw;
}

.home {
  margin: 60px 0 80px;
}
.home__logo {
  margin: 80px 0;
  display: flex;
  justify-content: center;
}
.home__logo img {
  width: 80vw;
}

.middle-section {
  padding: 90px 0;
  background: #1A396D;
}
.middle-section .text-container {
  color: white;
  font-weight: 300;
  display: grid;
  grid-template-columns: 1fr;
}
.middle-section .text-container .quote {
  font-weight: 300;
  color: white;
}
.middle-section .text-container button {
  width: 277px;
  justify-self: center;
}

.gallery {
  margin: 50px 0;
}
.gallery .text-container {
  display: grid;
  grid-template-columns: 1fr;
}
.gallery .text-container h2 {
  margin-bottom: 5px;
}
.gallery .text-container button {
  justify-self: center;
  width: 100%;
}
.gallery .gradient-relative {
  position: relative;
}
.gallery .gradient-relative .gradient {
  position: absolute;
  transform: translateX(5vw) translateY(15px);
  z-index: -1;
  width: 90vw;
  height: 55px;
  background: linear-gradient(180deg, #EDEDED 0%, rgba(255, 255, 255, 0) 100%);
  filter: drop-shadow(0px 0px 6px rgba(0, 0, 0, 0.05));
}

.footer {
  background-color: #1A396D;
}
.footer__main {
  height: 100%;
  display: grid;
  grid-template-columns: 1fr;
  color: #FFFFFF;
}
.footer__main h3, .footer__main h2 {
  margin: 52px 0 24px;
  text-align: center;
}
.footer__paragraph {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-bottom: 7px;
}
.footer__paragraph img {
  margin-right: 5px;
}
.footer__paragraph p {
  margin: 0 5px;
}
.footer__hours {
  display: flex;
  justify-content: center;
}
.footer__hours div {
  margin-right: 5px;
}
.footer__map {
  margin-top: 24px;
}
.footer_left {
  justify-self: center;
}
.footer_center {
  display: grid;
  grid-template-columns: 1fr;
  justify-self: center;
}
.footer_center p {
  padding: 0 20px;
  margin: 20px 0 10px;
}
.footer_center input {
  padding: 0 20px;
  height: 60px;
  width: 310px;
  border-radius: 12px;
  border: none;
  background: #FAFAFA;
}
.footer_center textarea {
  padding: 0 20px;
  height: 200px;
  width: 310px;
  border-radius: 12px;
  border: none;
  background: #FAFAFA;
  resize: none;
}
.footer_right {
  justify-self: center;
  margin-bottom: 50px;
}

.sec-footer {
  margin: 20px 0;
  text-align: center;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  cursor: default;
  font-family: "Gemunu Libre", sans-serif;
}

body {
  width: 100vw;
  overflow-x: hidden;
}

h1, .title {
  font-size: 36px;
}
@media screen and (min-width: 768px) {
  h1, .title {
    width: 100vw;
  }
}

h2 {
  font-size: 24px;
  margin-bottom: 19px;
}

h3 {
  margin-bottom: 11px;
}

p {
  font-size: 16px;
}

button {
  margin-top: 33px;
  background: #E30016;
  color: white;
  font-size: 24px;
  border: none;
  height: 56px;
  border-radius: 12px;
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
}

.container {
  margin: 0 auto;
}

.text-container {
  margin: 0 50px;
}
.text-container p, .text-container h2, .text-container h3 {
  text-align: center;
  font-weight: 400;
}

.quote {
  color: #757575;
}